.RestaurantsList {
  overflow-y: scroll;
  height: calc(100vh - 150px);
}

/* SCROLLBAR */

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #bbb;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}



